* {
      margin: 0;
      padding: 0;
  }
body {
      background-color: rgb(255, 255, 255);
      color: rgb(34, 34, 34);
      font-family: 'DM Sans', sans-serif;
      font-size: 16px;
      overflow-x: hidden;
      word-wrap: break-word;
  }
html {
      scroll-behavior: smooth;
  }
a {
      color: rgb(34, 34, 34);
      text-decoration: none;
  }
    h1, h2, h3, h4, h5, h5 {
          text-transform: uppercase;
      }
